.navigation {
	display: block;
	max-width: 100vw;
	padding: 0 0 var(--gap-m) 0;
	overflow-x: auto;
}

.navigation__list {
	display: flex;
	flex-flow: row nowrap;
	list-style: none;
	padding: 0;
	margin: 0;
}

.navigation__item-anchor {
	position: relative;
	display: inline-block;
	padding: var(--gap-s) var(--gap-l);
	color: var(--text-accent);
	font-weight: 600;
	text-transform: uppercase;
	text-decoration: none;
	white-space: nowrap;
	cursor: pointer;
}

.navigation__item--disabled > .navigation__item-anchor {
	cursor: default;
	opacity: .5;
}

.navigation__item:not(.navigation__item--disabled) > .navigation__item-anchor::after {
	content: '';
	position: absolute;
	right: var(--gap-l);
	left: var(--gap-l);
	bottom: 0;
	background: var(--text-accent);
	height: 2px;
	border-radius: 2px;
	opacity: 0;
	transition: opacity .2s linear;
}

.navigation__item--selected > .navigation__item-anchor::after,
.navigation__item:not(.navigation__item--disabled) > .navigation__item-anchor:hover::after {
	opacity: 1 !important;
}
