@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;800&display=swap');

/* define app theme */
html {
	font-family: 'Poppins', sans-serif;

	/* The background for the page and UI components.
	 */
	--back-normal: rgb(255, 255, 255);

	/* Accent color for UI components such as default buttons.
	 */
	--back-accent: linear-gradient(90deg, rgb(149, 0, 255), rgb(236, 42, 255));

	/* The colors of text.
	 * Must be clearly readable over their respective bases.
	 */
	--text-normal-raw: 0, 0, 0;
	--text-normal: rgb(var(--text-normal-raw));
	--text-accent-raw: 255, 255, 255;
	--text-accent: rgb(var(--text-accent-raw));

	/* three levels of text size */
	--size-s: 0.8rem;
	--size-m: 1.3rem;
	--size-l: 1.5rem;

	/* three levels of padding/margin */
	--gap-s: 0.25rem;
	--gap-m: 0.75rem;
	--gap-l: 1.5rem;
}

/* apply base colors */
body {
	--back: var(--back-normal);
	--text: var(--text-normal);
	background: var(--back);
	color: var(--text);
}

/* reset global styles */
* {
	padding: 0;
	margin: 0;
	font: inherit;
	color: inherit;
}
