.app-shell__wrapper {
	max-width: 1000px;
	margin: 0 auto;
	padding: 0 var(--gap-m);
}

.app-shell__top {
	--text: var(--text-accent);

	display: grid;
	align-items: center;
	justify-items: center;
	gap: var(--gap-m);
	grid-template-columns: 1fr 3fr 1fr;
	grid-template-rows: auto auto;
	grid-auto-flow: row;
	padding-top: var(--gap-m);
}

.app-shell__header {
	font-weight: 800;
	text-transform: uppercase;
	color: var(--text-accent);
	font-size: var(--size-m);
}

.app-shell__social {
	display: flex;
	flex-flow: row nowrap;
	justify-self: start;
}

.app-shell__wallet {
	justify-self: end;
}

.app-shell__menu {
	grid-column: 1 / span 3;
	grid-row: 2;
}
