.button {
	display: inline-flex;
	align-items: center;
	background: var(--back);
	color: var(--text);
	border-radius: 999px;
	border: none;
	outline: none;
	padding: var(--gap-m) var(--gap-l);
	text-transform: uppercase;
	white-space: nowrap;
	cursor: pointer;
}

.button--normal {
	--back: var(--back-normal);
	--text: var(--text-normal);
}

.button--accent {
	--back: var(--back-accent);
	--text: var(--text-accent);
}

.button--icon {
	position: relative;
	background: none;
	padding: var(--gap-m);
}

.button--icon::before {
	content: '';
	position: absolute;
	inset: 0;
	background: var(--back);
	border-radius: 999px;
	transform-origin: center center;
	transform: scale3d(0, 0, 1);
	opacity: 0;
	transition:
		opacity .2s linear,
		transform .2s ease;
}

.button--icon:hover::before {
	transform: scale3d(1.1, 1.1, 1);
	opacity: .3;
}

.button > .icon:not(:only-child) {
	margin-right: var(--gap-m);
}

@media (max-width: 600px) {
	.button__shrink-content {
		display: none;
	}

	.button > .icon {
		margin-right: 0 !important;
	}
}
